.checkoutProduct {
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid lightgray;
  }
  
  .checkoutProduct__info {
    padding-left: 20px;
  }
  
  .checkoutProduct__info > button {
    background: #fb641b;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 20%);
    border: none;
    color: #fff;
    padding: 18px 8px;
  }
  
  .checkoutProduct__image {
    object-fit: contain;
    width: 180px;
    height: 180px;
  }
  
  .checkoutProduct__rating {
    display: flex;
    color: #ffa41c;
  }
  
  .checkoutProduct__title {
    font-size: 17px;
    font-weight: 800;
  }