.login {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    /* background-color: #ccc; */
    background-color: black;
  }
  
  .login__logo {
    margin-top: 100px;
    margin-bottom: 20px;
    object-fit: contain;
    width: 100px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .login__container {
    display: flex;
    width: 400px;
    height: fit-content;
    flex-direction: column;
    border-radius: 5px;
    border: 1px solid lightgray;
    padding: 20px;
  }
  
  .login__container > h1 {
    font-weight: 500;
    margin-bottom: 20px;
    color: #fff;
    text-shadow: 2px 2px 10px black;
    text-align: center;
  }
  
  .login__container > form > h5 {
    margin-bottom: 5px;
    color: #fff;
    text-shadow: 1px 1px 10px #000;
  }
  
  .login__container > form > input {
    height: 30px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: none;
    background-color: white;
    width: 98%;
  }
  
  input:focus,
  placeholder {
    outline: none;
    color: #212121;
  }
  
  .login__container > p {
    margin-top: 15px;
    font-size: 12px;
    color: #fff;
  }
  
  .login__signInButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 20%);
    background: #ff9f00;
    cursor: pointer;
  }
  
  .login__registerButton {
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
  }