.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    box-shadow: 2px 2px 10px #000;
  }
  
  .product__rating {
    display: flex;
    color: #ffa41c;
  }
  
  .product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
  }
  
  .product > button {
    padding: 18px 18px;
    border-radius: 2px;
    box-shadow: 0 1px 2px 0 rgb(0 0 0 / 20%);
    border: none;
    float: left;
    background: #ff9f00;
    color: #fff;
    cursor: pointer;
  }
  
  .product__info {
    /* width: 100%; */
    height: 100px;
    margin-bottom: 15px;
  }
  
  .product__price {
    margin-top: 5px;
  }